import React, { useContext , useState} from "react";
import {
  BoldLink,
  BoxContainer,
  FormContainer,
  Input,
  MutedLink,
  SubmitButton,
} from "./common";
import { Marginer } from "../marginer";
import { AccountContext } from "./accountContext";
import axiosBase from "./axios";




export function SignupForm(props) {
  const { switchToSignin } = useContext(AccountContext);








  const [SingName, setSingName] = useState('');
  const [singEmail, setSingEmail] = useState('');
  const [singPosword ,setSingPosword] = useState('');
  const [Confairm, setConfairm] = useState('');


  const singdata ={
    'SingUpName': SingName,
    'SingUpEmail': singEmail,
    'SingUpPosword': singPosword,
    'SingUpposwordConfairm': Confairm
  }
  
  console.log(singdata);

function singup() {
  axiosBase.post('/singup', {
    singdata
  })
  .then(function (response) {
    console.log(response);
  })
  .catch(function (error) {
    console.log(error);
  });
}
    



  return (
    <BoxContainer> 
      <FormContainer>
        <Input type="text" placeholder="Name"  onChange={e => setSingName( e.target.value)}/>
        <Input type="email" placeholder="Email" onChange={e => setSingEmail( e.target.value)} />
        <Input type="password" placeholder="Password" onChange={e => setSingPosword( e.target.value)} />
        <Input type="password" placeholder="Confirm Password" onChange={e => setConfairm( e.target.value)} />
      </FormContainer>
      <Marginer direction="vertical" margin={10} />
      <SubmitButton type="submit"  onClick={singup}    >Signup</SubmitButton>
      <Marginer direction="vertical" margin="1em" />
      <MutedLink href="#">
        Already have an account?
        <BoldLink href="#" onClick={switchToSignin}>
          Signin
        </BoldLink>
      </MutedLink> 
    </BoxContainer>
  );
}
